.logo {
  background-color: #ac8836; }
  .logo .profile-photo {
    display: none;
    transform: scale(0, 0); }
  .logo .placeholder {
    transition: all 0.3s ease-in-out; }
  .logo .contact svg {
    transform: scale(0, 0); }

.logo.focused {
  transform-origin: top left;
  transform: scale(1.3, 1.3);
  position: relative; }
  .logo.focused .placeholder {
    position: relative;
    width: 30px !important;
    height: 30px !important; }
  .logo.focused .container {
    padding-right: unset;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center; }
    .logo.focused .container .header {
      margin-bottom: unset !important;
      background-color: unset !important; }
  .logo.focused .profile-photo {
    display: block;
    transform: scale(1, 1);
    margin: 10px; }
    .logo.focused .profile-photo img {
      border-radius: 50%; }
  .logo.focused .contact {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-right: 5px;
    margin-bottom: 2px; }
    .logo.focused .contact svg {
      color: #474040;
      padding: 2px;
      width: 25px;
      height: 25px;
      transform: scale(1, 1);
      transition: all 0.1s ease-in-out; }
      .logo.focused .contact svg:hover {
        transform: scale(1.1, 1.1);
        text-shadow: 0px 0px 10px 10px black; }

img {
  height: 50px;
  width: 50px; }

.education {
  background-color: #927a5c; }
  .education .container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    text-align: center;
    transform: scale(0, 0); }
    .education .container .info {
      padding-top: 10%;
      text-align: left; }
    .education .container .college {
      padding-bottom: 10px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start; }

.education.focused {
  transform: scale(1.7, 1.5);
  transform-origin: top; }
  .education.focused .placeholder {
    width: 30px !important;
    height: 30px !important;
    transform: rotate(-20deg); }
  .education.focused .name {
    background-color: rgba(190, 190, 18, 0.164);
