.app {
  box-sizing: border-box;
  font-size: 10px;
  display: flex;
  height: 100vh;
  flex-wrap: wrap;
  justify-content: space-evenly; }
  .app section {
    margin: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    width: 32%;
    height: 50%;
    position: relative;
    transition: all 0.3s ease-in-out;
    transform: scale(1, 1);
    opacity: 1;
    -webkit-animation: unset;
            animation: unset; }
    .app section .placeholder {
      position: absolute !important;
      width: 90% !important;
      height: 90% !important;
      margin: auto;
      color: rgba(105, 98, 98, 0.514);
      transition: all 0.3s ease-in-out; }
      .app section .placeholder path {
        text-shadow: 0px 0px 20px 4px black; }
    .app section .container {
      overflow-y: auto !important;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      padding: 0% 15% !important;
      transition: all 0.3s ease-in-out;
      text-align: left;
      transform: scale(0, 0); }
      .app section .container .header {
        margin-top: 2%;
        margin-bottom: 5%;
        font-size: 14px; }
      .app section .container .info {
        padding-top: 5px; }
  .app section.focused {
    z-index: 5;
    opacity: 1;
    border-radius: 8px;
    box-shadow: 0px 0px 30px 1px #535050;
    -webkit-animation: unset;
            animation: unset; }
    .app section.focused .placeholder {
      position: relative;
      top: 1%;
      left: 1%; }
      .app section.focused .placeholder path {
        width: 10%;
        height: 10%; }
    .app section.focused .container {
      transform: scale(1, 1); }
      .app section.focused .container .header {
        background-color: rgba(138, 190, 18, 0.164);
        padding: 2px 4px 1px 2px;
        display: inline-flex;
        width: auto;
        border-radius: 1px; }
  .app section.shaded {
    opacity: 0.2; }
    .app section.shaded .photo {
      display: none; }

.app.pulse .education {
  -webkit-animation: pulse-education 3s infinite;
          animation: pulse-education 3s infinite; }

.app.pulse .photography {
  -webkit-animation: pulse-photography 3s infinite 1s;
          animation: pulse-photography 3s infinite 1s; }

.app.pulse .projects {
  -webkit-animation: pulse-projects 3s infinite 2s;
          animation: pulse-projects 3s infinite 2s; }

.app.pulse .work-experience {
  -webkit-animation: pulse-work-exp 3s infinite 3s;
          animation: pulse-work-exp 3s infinite 3s; }

.app.pulse .logo {
  -webkit-animation: pulse-logo 3s infinite 4s;
          animation: pulse-logo 3s infinite 4s; }

.app.pulse .technology-stack {
  -webkit-animation: pulse-tech-stack 3s infinite 5s;
          animation: pulse-tech-stack 3s infinite 5s; }

